.wing {
  position: absolute;
  top: 200px;
  right: -220px;
  width: 150px;
  height: 271px;
  border: 1px solid #777;
  animation-name: upDown;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-fill-mode: both;
}

@keyframes upDown {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0px);
  }
}
.wing img {
  width: 100%;
  height: auto;
}

.wing .pass {
  position: absolute;
  top: 118px;
  left: 50px;
  font-family: "S-CoreDream-3Light";
  font-size: 12px;
}

